"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Loader2, Store, Calendar, CalendarDays } from "lucide-react"

interface StoreData {
  store: string
  items: { [key: string]: number }
  total: number
}

interface ApiResponse {
  monthly: StoreData[]
  today: StoreData[]
  error?: string
}

const STORE_ORDER = [
  "SPLASH'N'GO!前橋50号店",
  "SPLASH'N'GO!伊勢崎韮塚店",
  "SPLASH'N'GO!高崎棟高店",
  "SPLASH'N'GO!足利緑町店",
  "SPLASH'N'GO!新前橋店",
  "SPLASH'N'GO!太田新田店",
]

export default function DashboardView() {
  const [selectedPeriod, setSelectedPeriod] = useState("2025-11")
  const [monthlyData, setMonthlyData] = useState<StoreData[]>([])
  const [todayData, setTodayData] = useState<StoreData[]>([])
  const [loading, setLoading] = useState(false)

  // 2025年11月から2030年12月までの期間を生成
  const generatePeriods = () => {
    const periods = []
    for (let year = 2025; year <= 2030; year++) {
      const endMonth = year === 2030 ? 12 : 12
      const startMonth = year === 2025 ? 11 : 1
      for (let month = startMonth; month <= endMonth; month++) {
        periods.push(`${year}-${String(month).padStart(2, "0")}`)
      }
    }
    return periods
  }

  const periods = generatePeriods()

  useEffect(() => {
    fetchData()
  }, [selectedPeriod])

  const fetchData = async () => {
    setLoading(true)
    try {
      const response = await fetch(`/api/dashboard?period=${selectedPeriod}`)
      const result: ApiResponse = await response.json()

      if (result.error || !result.monthly || !result.today) {
        console.error("[v0] API error:", result.error || "Invalid response format")
        setMonthlyData([])
        setTodayData([])
        return
      }

      const filteredMonthly = result.monthly.filter(
        (store: StoreData) => store.store && store.store !== "0" && store.store.trim() !== "",
      )
      const filteredToday = result.today.filter(
        (store: StoreData) => store.store && store.store !== "0" && store.store.trim() !== "",
      )

      const sortByOrder = (data: StoreData[]) => {
        return [...data].sort((a, b) => {
          const indexA = STORE_ORDER.indexOf(a.store)
          const indexB = STORE_ORDER.indexOf(b.store)
          // 順序リストにない店舗は末尾に
          if (indexA === -1 && indexB === -1) return 0
          if (indexA === -1) return 1
          if (indexB === -1) return -1
          return indexA - indexB
        })
      }

      setMonthlyData(sortByOrder(filteredMonthly))
      setTodayData(sortByOrder(filteredToday))
    } catch (error) {
      console.error("[v0] Error fetching data:", error)
      setMonthlyData([])
      setTodayData([])
    } finally {
      setLoading(false)
    }
  }

  const formatPeriodLabel = (period: string) => {
    const [year, month] = period.split("-")
    return `${year}年${Number.parseInt(month)}月度`
  }

  // 全体の合計を計算
  const monthlyTotal = monthlyData.reduce((sum, store) => sum + store.total, 0)
  const todayTotal = todayData.reduce((sum, store) => sum + store.total, 0)

  const getTodayDataForStore = (storeName: string): StoreData | undefined => {
    return todayData.find((s) => s.store === storeName)
  }

  const StoreCard = ({ store, totalCount }: { store: StoreData; totalCount: number }) => {
    const percentage = totalCount > 0 ? ((store.total / totalCount) * 100).toFixed(1) : "0"
    const todayStore = getTodayDataForStore(store.store)
    const todayStoreTotal = todayStore?.total || 0

    // 全てのアイテムを取得（月間と本日を合わせて）
    const allItems = new Set([...Object.keys(store.items), ...(todayStore ? Object.keys(todayStore.items) : [])])

    return (
      <Card className="border border-gray-200 bg-white shadow-sm hover:shadow-md transition-shadow">
        <CardContent className="p-5">
          <h3 className="text-base font-bold text-gray-900 mb-4">{store.store}</h3>

          {/* 月間と本日の合計を横並びで表示 */}
          <div className="grid grid-cols-2 gap-3 mb-4">
            <div className="bg-blue-50 rounded-lg p-3">
              <div className="flex items-center gap-1 text-xs text-blue-600 mb-1">
                <Calendar className="h-3 w-3" />
                <span>月間</span>
              </div>
              <span className="text-2xl font-bold text-blue-600">{store.total}</span>
              <span className="text-sm text-blue-600 ml-1">台</span>
            </div>
            <div className="bg-green-50 rounded-lg p-3">
              <div className="flex items-center gap-1 text-xs text-green-600 mb-1">
                <CalendarDays className="h-3 w-3" />
                <span>本日</span>
              </div>
              <span className="text-2xl font-bold text-green-600">{todayStoreTotal}</span>
              <span className="text-sm text-green-600 ml-1">台</span>
            </div>
          </div>

          <div className="w-full h-1.5 bg-gray-100 rounded-full mb-1">
            <div className="h-1.5 bg-blue-600 rounded-full transition-all" style={{ width: `${percentage}%` }}></div>
          </div>
          <p className="text-xs text-gray-500 mb-4">全体の {percentage}%</p>

          <div className="border-t border-gray-100 pt-3">
            <p className="text-xs text-gray-500 mb-2">内訳</p>
            <div className="space-y-2">
              {Array.from(allItems)
                .sort((a, b) => {
                  const countA = store.items[a] || 0
                  const countB = store.items[b] || 0
                  return countB - countA
                })
                .map((item) => {
                  const monthlyCount = store.items[item] || 0
                  const todayItemCount = todayStore?.items[item] || 0
                  return (
                    <div
                      key={item}
                      className="flex justify-between items-center text-sm bg-gray-50 rounded px-2 py-1.5"
                    >
                      <span className="text-gray-700 truncate mr-2 font-medium">{item}</span>
                      <div className="flex items-center gap-2 whitespace-nowrap">
                        <span className="text-blue-600 font-semibold">{monthlyCount}台</span>
                        <span className="text-gray-300">|</span>
                        <span className="text-green-600 font-semibold">{todayItemCount}台</span>
                      </div>
                    </div>
                  )
                })}
            </div>
            {/* 内訳の凡例 */}
          </div>
        </CardContent>
      </Card>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="mx-auto max-w-7xl space-y-6">
        <div className="flex items-center justify-between rounded-xl bg-primary p-6 shadow-md">
          <div>
            <h1 className="text-3xl font-bold text-primary-foreground">DASHBOAD</h1>
            <p className="mt-1 text-primary-foreground/80"></p>
          </div>

          <div className="w-64">
            <Select value={selectedPeriod} onValueChange={setSelectedPeriod}>
              <SelectTrigger className="border-2 border-primary-foreground/20 bg-white text-foreground font-semibold shadow-sm">
                <SelectValue placeholder="期間を選択" />
              </SelectTrigger>
              <SelectContent>
                {periods.map((period) => (
                  <SelectItem key={period} value={period}>
                    {formatPeriodLabel(period)}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>

        <Card className="border border-gray-200 bg-white shadow-sm">
          <CardHeader className="pb-4">
            <div className="flex items-center gap-2">
              <Store className="h-5 w-5 text-blue-600" />
              <CardTitle className="text-lg font-semibold text-gray-900">
                {formatPeriodLabel(selectedPeriod)} - 店舗別集計結果
              </CardTitle>
            </div>
            <div className="flex items-center gap-6 text-sm mt-2">
              <div className="flex items-center gap-2">
                <span className="w-3 h-3 bg-blue-600 rounded-full"></span>
                <span className="text-gray-600">月間合計:</span>
                <span className="font-bold text-blue-600 text-lg">{monthlyTotal}台</span>
              </div>
              <div className="flex items-center gap-2">
                <span className="w-3 h-3 bg-green-600 rounded-full"></span>
                <span className="text-gray-600">本日合計:</span>
                <span className="font-bold text-green-600 text-lg">{todayTotal}台</span>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            {loading ? (
              <div className="flex h-64 items-center justify-center">
                <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
              </div>
            ) : monthlyData.length === 0 ? (
              <div className="flex h-64 items-center justify-center text-gray-500">データが見つかりませんでした</div>
            ) : (
              <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                {monthlyData.map((store) => (
                  <StoreCard key={store.store} store={store} totalCount={monthlyTotal} />
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
